@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-primary: #0064eb;
}

html,
body {
  background: #f9f9f9;
  margin: 0;
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  line-height: 17px;
}

.layout {
  display: -webkit-flex;
  display: flex;
}

header {
  background: var(--color-primary);
  height: 72px;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  box-sizing: border-box;
}

@media screen and (min-width: 480px) {
  header {
    padding: 0 20px;
  }
}

.logo {
  margin: 0 20px;
}

.search {
  height: 40px;
  max-width: 500px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-right: 20px;
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 6px;
}

.searchImage {
  margin-left: 19px;
  margin-right: 13px;
}

.searchInput {
  border: none;
  outline: none;
  height: 24px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding-right: 10px;
  width: 85%;
  max-width: 420px;
  font: inherit;
}

.searchInput::-webkit-input-placeholder {
  color: #808080;
  letter-spacing: 0.01em;
}

.searchInput:-ms-input-placeholder {
  color: #808080;
  letter-spacing: 0.01em;
}

.searchInput::placeholder {
  color: #808080;
  letter-spacing: 0.01em;
}

.userProfile {
  width: 400px;
  padding: 120px 50px 0 50px;
}

.avatar {
  width: 280px;
  height: 280px;
  border-radius: 50%;
}

.userFullName {
  margin-top: 29px;
  font-weight: 600;
  font-size: 26px;
  line-height: 130%;
}

.userName {
  margin-top: 12px;
  font-size: 18px;
  line-height: 22px;
  font-weight: normal;
  text-decoration: none;
  color: var(--color-primary);
}

.follow {
  margin-top: 25px;
  width: 100%;
  height: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  font-size: 16px;
  line-height: 24px;
}

.figure {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 20px;
}

.figure > img {
  margin-right: 9px;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 100px 56px 0 128px;
  max-width: 877px;
}

.repo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: white;
  margin-bottom: 24px;
  padding: 24px 32px;
}

.repo > a {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-decoration: none;
  color: var(--color-primary);
}

.repo > span {
  font-size: 16px;
  line-height: 19px;
}

main > h2 {
  font-weight: 600;
  font-size: 32px;
  line-height: 130%;
}

.paginationContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.paginationPagesContainer {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-align-items: center;
          align-items: center;
}

.paginationPage {
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
  margin: 0 5px;
  padding: 2px 6px;
}

.paginationPage:hover,
.paginationPage:active,
.paginationActive {
  background: var(--color-primary);
  fill: white;
  color: white;
}

.placeholder {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.placeholderText {
  height: 62px;
  width: 210px;
  margin-top: 47px;
  font-size: 22px;
  line-height: 31px;
  text-align: center;
  color: #808080;
}

